/* Large desktops */

body,
html {
  width: 100%;
  height: 100%;
}

main {
  position: relative;
}

html > div[style] {
  display: none !important;
}

ul,
menu,
ol,
section,
figure,
aside {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

.wrapper {
  position: relative;
  display: block;
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 60px;
  overflow: hidden;
}

.page-breadcrumbs {
  margin-bottom: 24px;
  padding: 0;
  font-size: 14px;
}

.page-breadcrumbs a,
.page-breadcrumbs strong {
  margin: 0 10px;
}

.page-breadcrumbs > span > span > a {
  margin-left: 0;
}

.page-breadcrumbs strong {
  margin-right: 0;
}

.stop-scrolling {
  position: fixed;
  width: 100%;
  height: 100%;
  max-height: 100%;
  overflow-y: scroll;
}

section.section {
  padding-top: 30px;
  padding-bottom: 30px;
}

section.section .scroll-top {
  display: none !important;
}

ul.pagination {
  float: left !important;
  width: 100%;
  padding-left: 15px;
}

body,
html {
  color: var(--color-gray-primary);
  font-size: 13px;
  font-weight: normal;
  font-family: var(--main-font);
  line-height: 1.3;
  -webkit-font-smoothing: antialiased;
}

h1,
.h1 {
  margin: 26.66667px 0;
  font-size: 23px;
  line-height: 32px;
}

h2,
.h2 {
  margin: 26.66667px 0;
  font-size: 21px;
  line-height: 30px;
}

h3,
.h3 {
  margin: 26.66667px 0;
  font-size: 19px;
  line-height: 24px;
}

h4,
.h4 {
  margin: 26.66667px 0;
  font-size: 17px;
  line-height: 24px;
}

h5,
.h5 {
  margin: 26.66667px 0;
  font-size: 15px;
  line-height: 24px;
}

h6,
.h6 {
  margin: 26.66667px 0;
  font-size: 13px;
  line-height: 20px;
}

.hero__headline {
  margin: 72px 0 26px 0;
  font-size: 24px;
  line-height: 32px;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: var(--second-font);
  padding: 0;
  text-transform: uppercase;
  font-weight: 600;
}

h1:first-child,
h2:first-child,
h3:first-child,
h4:first-child,
h5:first-child,
h6:first-child,
.h1:first-child,
.h2:first-child,
.h3:first-child,
.h4:first-child,
.h5:first-child,
.h6:first-child {
  margin-top: 0;
  margin-left: 0;
}

h1:last-child,
h2:last-child,
h3:last-child,
h4:last-child,
h5:last-child,
h6:last-child,
.h1:last-child,
.h2:last-child,
.h3:last-child,
.h4:last-child,
.h5:last-child,
.h6:last-child {
  margin-right: 0;
  margin-bottom: 0;
}

strong {
  font-family: var(--second-font);
  font-size: 16px;
  line-height: 20px;
}

small {
  margin: 16px 0;
  padding: 0;
  font-size: 13px;
  line-height: 22px;
}

b,
strong,
label,
.label {
  font-weight: 500;
}

p {
  margin: 14px 0;
  padding: 0;
}

p:last-child {
  margin-bottom: 0;
}

a {
  margin: 0;
  padding: 0;
  color: #3a3d45;
}

a,
a:focus,
a:active,
a:hover,
a:visited {
  position: relative;
  color: #666666;
  text-decoration: none;
  transition: color var(--transition-duration-primary) var(--transition-function-primary);
}

/* iPad fix */

input,
textarea,
select {
  -webkit-appearance: none;
  /*disable shaddow nav ipad*/
  -moz-appearance: none;
  /*disable shaddow nav ipad*/
  border-radius: 0;
  /*disable border-radius ipad*/
}

