    padding-top: 62px;
  }

  .wrapper {
    max-width: 100%;
    padding: 0 12px;
  }

  h1,
  .h1 {
    margin: 15px 0;
  }

  h2,
  .h2 {
    margin: 15px 0;
  }

  h3,
  .h3 {
    margin: 15px 0;
  }

  h4,
  .h4 {
    margin: 15px 0;
  }

  h5,
  .h5 {
    margin: 15px 0;
  }

  h6,
  .h6 {
    margin: 15px 0;
  }

  .hero__headline {
    margin: 48px 0 24px 0;
  }

  p {
    margin: 8px 0;
  }

  .single-page-tabs .tabs__nav {
    flex-direction: column;
    margin-bottom: 24px;
  }

  .single-page-tabs .tabs__nav-link {
    padding-bottom: 2px;
    color: var(--color-dark-primary) !important;
    font-weight: 600;
    text-overflow: ellipsis;
    text-shadow: none;
    background: linear-gradient(90deg, var(--color-blue-primary), var(--color-blue-secondary));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    box-decoration-break: clone;
  }

  .single-page-tabs .tabs__nav-link:after {
    position: absolute;
    bottom: 1px;
    left: 0;
    width: 0;
    height: 2px;
    background: linear-gradient(90deg, var(--color-blue-primary), var(--color-blue-secondary));
    transition: width var(--transition-duration-primary) var(--transition-function-secondary);
    content: "";
  }

  .single-page-tabs .tabs__nav-link_current {
    color: var(--color-blue-primary) !important;
  }

  .single-page-tabs .tabs__nav-link_current:after {
    width: 100%;
  }

  .spoilers .sidebar__desc {
    padding-top: 0;
  }

  .spoilers .sidebar .form__headline {
    padding: 24px 0 0 0;
  }
}

@media print {
  .container {
    width: 100% !important;
  }

  a[href]:after,
  abbr[title]:after,
  a[href^="#"]:after {
    content: none !important;
  }

  footer .footer-links {
    display: none !important;
  }

  .main-header {
    padding: 40px 0 !important;
  }

  nav {
    display: none !important;
  }
}